// Define props
const props = defineProps({
  token: {
    type: String,
    default: null
  }
})

const loading = ref(true)
const error = ref(null)
const submitting = ref(false)
const submissionType = ref('invited') // 'invited' or 'open'

const vendorInfo = ref({
  vendor_name: '',
  vendor_email: '',
  vendor_phone: '',
  company_name: ''
})

const completionStatus = ref({
  company: 100,
  responses: 60,
  documents: 40,
  personnel: 80
})

