  {
    id: "technical_proposal",
    title: "Technical Competence",
    weight: 40,
    description: "Demonstrate your technical capabilities for cloud infrastructure management",
    type: "narrative",
    required: true
  },
  {
    id: "implementation_approach",
    title: "Implementation Approach",
    weight: 30,
    description: "Describe your methodology for implementing the solution",
    type: "narrative",
    required: true
  },
  {
    id: "proposed_value", 
    title: "Commercial Competitiveness",
    weight: 20,
    description: "Provide detailed pricing breakdown including costs, phases, and payment terms",
    type: "pricing",
    required: true
  },
  {
    id: "timeline",
    title: "Project Timeline",
    weight: 10,
    description: "Proposed project timeline and milestones",
    type: "timeline",
    required: true
  }
]

const documents = [
  { name: "Company Profile", uploaded: true, required: true },
  { name: "Technical Certifications", uploaded: true, required: true },
  { name: "Reference Letters", uploaded: false, required: true },
  { name: "Insurance Certificate", uploaded: true, required: false },
  { name: "Financial Statements", uploaded: false, required: true }
]

const keyPersonnel = ref([
  { role: "Project Manager", name: "Jane Doe", experience: "8 years", certifications: ["PMP", "ITIL"] },
  { role: "Technical Lead", name: "Mike Johnson", experience: "12 years", certifications: ["AWS Solutions Architect", "Kubernetes"] }
])

const rfpDocuments = ref([
  {
    id: 1,
    name: 'RFP Requirements Document',
    size: '2.4 MB',
    type: 'PDF',
    url: '/documents/rfp-requirements.pdf'
  },
  {
    id: 2,
    name: 'Technical Specifications',
    size: '1.8 MB',
    type: 'PDF',
    url: '/documents/technical-specs.pdf'
  },
  {
    id: 3,
    name: 'Evaluation Criteria',
    size: '856 KB',
    type: 'PDF',
    url: '/documents/evaluation-criteria.pdf'
  }
])

const proposalData = ref({
  technical_proposal: '',
  implementation_approach: '',
  proposed_value: '',
  payment_terms: '',
  start_date: '',
  completion_date: '',
  additional_info: '',
  timeline: ''
})

const overallProgress = computed(() => {
  return Math.round(
    (completionStatus.value.company + completionStatus.value.responses + completionStatus.value.documents + completionStatus.value.personnel) / 4
  )
})

const loadInvitationData = async () => {
  try {
    // Use token from props (passed from entry point)
    const token = props.token
    console.log('VendorPortal: Received token prop:', token)
    console.log('VendorPortal: Props object:', props)
    
    if (token) {
      // Handle invited vendor access
      submissionType.value = 'invited'
      console.log('Loading invitation data for token:', token)
      
      // Simulate loading delay
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Use mock data for now - this ensures the portal works
      vendorInfo.value = {
        vendor_name: 'John Smith',
        vendor_email: 'john.smith@techcorp.com',
        vendor_phone: '+1 (555) 123-4567',
        company_name: 'TechCorp Solutions'
      }
      
      console.log('Vendor portal loaded successfully with mock data')
      
      // Try to load from API as well (optional)
      try {
        const invitationData = await rfpResponseService.getInvitationByToken(token)
        if (invitationData && invitationData.invitation) {
          // Update with real data if available
          vendorInfo.value = {
            vendor_name: invitationData.invitation.vendor_name || vendorInfo.value.vendor_name,
            vendor_email: invitationData.invitation.vendor_email || vendorInfo.value.vendor_email,
            vendor_phone: invitationData.invitation.vendor_phone || vendorInfo.value.vendor_phone,
            company_name: invitationData.invitation.company_name || vendorInfo.value.company_name
          }
          console.log('Updated with API data:', invitationData)
        }
      } catch (apiError) {
        console.log('API not available, using mock data:', apiError.message)
      }
      
    } else {
      error.value = 'Invalid access. Please use a valid invitation link or RFP access link.'
      return
    }
    
  } catch (err) {
    console.error('Error loading data:', err)
    error.value = 'Failed to load data. Please check your link and try again.'
  } finally {
    loading.value = false
  }
}

const formatDate = (dateString) => {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}

const downloadDocument = (document) => {
  // In a real application, this would trigger a download
  console.log('Downloading document:', document.name)
  // For demo purposes, we'll just show an alert
  alert(`Downloading ${document.name}`)
}

const handleAutoSave = () => {
  console.log('Auto-saving progress...')
}

const saveDraft = async () => {
  try {
    submitting.value = true
    
    // Use token from props
    const token = props.token
    
    const draftData = {
      vendor_info: vendorInfo.value,
      proposal_data: proposalData.value,
      documents: documents.value,
      key_personnel: keyPersonnel.value,
      timestamp: new Date().toISOString(),
      token: token
    }
    
    // Save to localStorage (always works)
    localStorage.setItem('rfp_draft_' + (token || 'default'), JSON.stringify(draftData))
    console.log('Draft saved locally:', draftData)
    
    // Try to save via API if available
    if (submissionType.value === 'invited' && token) {
      try {
        await rfpResponseService.saveDraft(token, draftData)
        console.log('Draft also saved via API')
      } catch (apiError) {
        console.log('API save failed, but local save succeeded:', apiError.message)
      }
    }
    
    alert('Draft saved successfully!')
  } catch (err) {
    console.error('Error saving draft:', err)
    alert('Failed to save draft')
  } finally {
    submitting.value = false
  }
}

const submitProposal = async () => {
  try {
    submitting.value = true
    
    // Validate required fields
    if (!proposalData.value.technical_proposal || !proposalData.value.proposed_value) {
      alert('Please fill in all required fields (Technical Competence and Commercial Competitiveness)')
      return
    }
    
    if (submissionType.value === 'open') {
      // Additional validation for open submissions
      if (!vendorInfo.value.company_name || !vendorInfo.value.vendor_name || !vendorInfo.value.vendor_email) {
        alert('Please fill in all required vendor information')
        return
      }
    }
    
    // Use token from props
    const token = props.token
    
    const submissionData = {
      vendor_info: vendorInfo.value,
      proposal_data: proposalData.value,
      documents: documents.value,
      key_personnel: keyPersonnel.value,
      submitted_at: new Date().toISOString(),
      token: token,
      status: 'submitted'
    }
    
    // Save to localStorage (always works)
    localStorage.setItem('rfp_submitted_' + (token || 'default'), JSON.stringify(submissionData))
    console.log('Proposal saved locally:', submissionData)
    
    // Try to submit via API if available
    if (submissionType.value === 'invited' && token) {
      try {
        await rfpResponseService.submitResponse(token, submissionData)
        console.log('Proposal also submitted via API')
      } catch (apiError) {
        console.log('API submission failed, but local save succeeded:', apiError.message)
      }
    }
    
    alert('Proposal submitted successfully! Your proposal has been received and is under review.')
    
    // Disable form after successful submission
    document.querySelectorAll('textarea, input').forEach(input => {
      if (!input.readOnly) input.disabled = true
    })
    document.querySelectorAll('button').forEach(btn => {
      btn.disabled = true
    })
    
  } catch (err) {
    console.error('Error submitting proposal:', err)
    alert('Failed to submit proposal')
  } finally {
    submitting.value = false
  }
}

onMounted(() => {
  loadInvitationData()
})
</script>

